const experienceAccardione = (e, textContainer, textButton) => {
    let target = e.target;
    let closest = target.closest('.info-bullet__button--more');

    if (!closest) {
        return
    }

    textButton.forEach((button, index) => {
        if (button === closest) {
            textContainer[index].style.height = 'auto';
            textContainer[index].style.maxHeight = 800 +'px';
            button.parentNode.style.top = 0 + 'px';
            button.parentNode.style.height = 0 + 'px';
            button.remove();
        }
    });
}

export default experienceAccardione;


